* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

/* ----- color-swither ----- */

.switcher button {
  padding: 20px 40px;
  font-size: large;
  margin-top: calc(40%/2);
}

.switcher {
  width: 100%;
  text-align: center;
}

.switcher p {
  text-align: left;
}

/* ----- timer ----- */

.timer {
  display: flex;
}

.field {
  margin-top: 10px;
  margin-right: 20px;
}

.value {
  display: flex;
  justify-content: center;
  font-size: 40px;
}

.label {
  display: flex;
  justify-content: center;
  font-size: large;
}
